@keyframes shine-TTT {
  0% {
    color: #6b3918;
    text-shadow: none;
  }
  100% {
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffffe6, 0 0 1.5rem #ffbd65,
      -0.2rem 0.1rem 1rem #ffbd65, 0.2rem 0.1rem 1rem #ffbd65,
      0 -0.5rem 2rem #ff8324, 0 0.5rem 3rem #ff8324;
  }
}
.sign {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50%;
  background-image: radial-gradient(
    ellipse 50% 35% at 50% 50%,
    #6b3918,
    transparent
  );
  transform: translate(-50%, -50%);
  letter-spacing: 2;
  left: 50%;
  top: 10%;
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 5em;
  color: #ffffe6;
  text-align: center;
  text-shadow: 0 0 0.6rem #ffffe6, 0 0 1.5rem #ffbd65,
    -0.2rem 0.1rem 1rem #ffbd65, 0.2rem 0.1rem 1rem #ffbd65,
    0 -0.5rem 2rem #ff8324, 0 0.5rem 3rem #ff8324;
  animation: shine-TTT 2s forwards, flicker 3s infinite;
}